body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(220, 220, 220);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a{text-decoration: none;}

ul{
	margin: 0;
	padding: 0;
	list-style: none;
}

.link_bg {
  display: grid;
  justify-items: center;
  margin-right: 15px;
  width: 4em;
  transition: 0.5s;
}
.link_bg:hover {
  background-color: rgba(210, 210, 210, 0.5);
  text-shadow: 5px 4px 6px black;
  font-size: 1.1em;
}

.nav_bg {
  height: 4.5em;
}

.component_bg {
  margin-top: 20px;
  background-color: rgb(210, 210, 210);
}
.mr {
  margin-right: 5px;
}
.side{
	width: 200px;
	margin: 20px;
}

.menu a{
	background: #3d3d3d;
	color: #fff;
	padding: 10px;
	display: block;
	border-bottom: 1px solid #666;
	transition: 0.5s all;
}

.menu a:hover{
	background: #666;
	padding: 10px 0 10px 20px;
}

.menu li:first-child .a
.menu li .menu__drop li:first-child a{
	border-radius: 3px 3px 0 0;
}

.menu li:last-child .a
.menu li .menu__drop li:last-child a{
	border-radius: 0 0 3px 3px;
	border-bottom: 0;
}

.menu li .menu__drop li a{
	border-radius: 0;
	border-bottom: 1px solid #666;
}

/*Всплывающие меню*/

.menu__list{
	position: relative;
}

.menu__drop{
	position: absolute;
	width: 100%;
	left: 100%;
	top: -9999em;
	border-left: 10px solid transparent;
	opacity: 0;
	transition: 0.5s opacity;
}

.menu__list:hover .menu__drop{
	opacity: 1;
	top: 0;
}

/*Иконки для всплывающих менюшек*/

.menu__list::after{
	content: " ";
	position: absolute;
	border: 5px solid;
	border-color: transparent transparent transparent #eee;
	top: 1em;
	right: 0.7em;
	transition: 0.5s;
}

.menu__list:hover::after{
	-webkit-transform: scaleX(-1);
	        transform: scaleX(-1);
}

